
 .hero {
  position: relative;
  width: 100%;
  /* min-height: px;  */
  overflow: visible;      
  margin-top: 80px;   
}

.bgImage {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 900px;             
  overflow: hidden;
  border-radius: 0 0 20px 20px;

}

.image {
  object-fit: cover;
  object-position: right center;
}

.logoContainer {
  margin-top: 2rem;
  display: flex;
  justify-content: flex-end;
  /* margin-left: 2rem; */

}


.overlay {
  position: relative;
  z-index: 3;
  min-height: 600px;          
  background: linear-gradient(to right, rgba(131,105,255,0.12), transparent 60%);
  display: flex;
  align-items: flex-start;
  padding-top: 160px;          
}


.container {
  width: 100%;
  max-width: 1280px;          
  margin: 0 auto;
  padding: 0 2rem;
}


.tagline {
  font-size: 0.9rem;
  color: #2a7ae2;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 1rem;
}

.title {
  
  color: #13195c;
  line-height: 1.1;
  font-family: Plus Jakarta Sans;
  margin-bottom: 1.5rem;
  font-size: 68px;
font-style: normal;
font-weight: 500;

}
 
.highlight {
  color: #13195c;
   font-family: Instrument Serif;
   font-style: italic;
   font-family: "Instrument Serif";
font-size: 68px;

font-weight: 400;
}

.ctaButton {
  display: inline-block;
  background-color: #13195c;
  color: white;
  padding: 0.8rem 1.8rem;
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s;
  box-shadow: 0 4px 10px rgba(19, 25, 92, 0.3);
}

.ctaButton:hover {
  background-color: #0a0e33;
  transform: translateY(-2px);
}

/* Cards */
.cards {
  display: flex;
  gap: 1.5rem;
  margin-top: 3rem;
  flex-wrap: wrap;

}

.card {
  background: white;
  padding: 1.5rem;
  border-radius: 16px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  flex: 1;
  min-width: 200px;
  max-width: 240px;
  transition: transform 0.3s;
}

.card:hover {
  transform: translateY(-5px);
}

.card h3 {
  font-size: 1.1rem;
  color: #13195c;
  margin: 0.5rem 0;
}

.card p {
  font-size: 0.9rem;
  color: #555;
  margin: 0.5rem 0;
}

.number {
  font-size: 1.8rem;
  font-weight: bold;
  color: #2a7ae2;
}

.explore {
  color: #2a7ae2;
  font-size: 0.9rem;
  text-decoration: none;
  font-weight: 500;
}

.explore::after {
  content: ' â†’';
}

.icon {
  display: flex;
  justify-content: center;
  margin-bottom: 0.5rem;
}

.author {
  font-style: italic;
  color: #2a7ae2;
  margin-top: 0.5rem;
}

/* RESPONSIVE */

@media (max-width: 1300px) {
  .container {
    max-width: 1000px;
  }
  .title {
    font-size: 3rem;
  }
}

@media (max-width: 992px) {
  .overlay {
    padding-top: 100px;
  }
  .title {
    font-size: 2.5rem;
  }
  .cards {
    justify-content: center;
  }
  .card {
    min-width: 180px;
    max-width: 200px;
  }
}

@media (max-width: 768px) {
  .hero {
    min-height: 700px;
  }
  .overlay {
    padding-top: 80px;
    background: linear-gradient(to bottom, rgba(131, 105, 255, 0.2), transparent);
  }
  .title {
    font-size: 2.2rem;
    br {
      display: none;
    }
  }
  .cards {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
  }
  .card {
    max-width: 300px;
    width: 100%;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .title {
    font-size: 1.8rem;
  }
  .ctaButton {
    padding: 0.7rem 1.5rem;
    font-size: 0.9rem;
  }
}